
.data-dialog {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 88;
  background: rgba(0,0,0,0.3);
  transition: all 0.4s linear;
  transition-timing-function: cubic-bezier(0, 0.74, 0.52, 1.38);
}
.data-dialog.dialog-enter-active {
  opacity: 1;
}
.data-dialog.dialog-enter,
.data-dialog.dialog-leave-active {
  opacity: 0;
}
.data-dialog .ui-dialog {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 215px;
  background: #fff;
}
.data-dialog .ui-dialog ul {
  position: relative;
  overflow: hidden;
  zoom: 1;
  background: #eee;
}
.data-dialog .ui-dialog ul li {
  float: left;
  width: 50%;
  text-align: center;
  font-size: 16px;
  height: 46px;
  line-height: 46px;
  color: #747474;
}
.data-dialog .ui-dialog ul li.sure-btn {
  color: #f76a67;
}
.data-dialog .ui-dialog .ui-confirm-title {
  text-align: center;
  font-size: 14px;
  color: #333;
  line-height: 25px;
  padding: 20px 0 20px 0;
}
.data-dialog .ui-dialog .ui-confirm-title .ui-scroller-mask {
  -webkit-mask: -webkit-gradient(linear, 0 30%, 0 0, from(#debb47), to(rgba(36,142,36,0)));
  position: relative;
}
.data-dialog .ui-dialog .ui-confirm-title .ui-scroller-mask p {
  position: absolute;
  top: 29px;
  height: 30px;
  width: 100%;
  margin: 0;
  z-index: -1;
  border: 1px solid #e2e2e2;
  border-left: none;
  border-right: none;
}
.data-dialog .ui-dialog .ui-confirm-title .ui-scroller {
  -webkit-mask: -webkit-gradient(linear, 0 10%, 0 100%, from(#debb47), to(rgba(36,142,36,0)));
}
.data-dialog .ui-dialog .ui-confirm-title .ui-scroller div {
  width: 32%;
  display: inline-block;
  position: relative;
  height: 125px;
  overflow: hidden;
}
.data-dialog .ui-dialog .ui-confirm-title .ui-scroller dl {
  position: absolute;
  width: 100%;
  margin: 0;
  top: 30px;
}
.data-dialog .ui-dialog .ui-confirm-title .ui-scroller dd {
  padding: 0;
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
  height: 30px;
  line-height: 30px;
  font-size: 16px;
}


