
.index-page {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: url("../assets/xz_bg.jpg") no-repeat;
  background-size: 100% 100%;
}
.index-page .count-wrapper {
  padding: 1.8rem 0.6rem 0.3rem 0.6rem;
  overflow: hidden;
}
.index-page .count-wrapper .list-item {
  position: relative;
  float: left;
  margin: 0 5%;
  width: 25%;
  height: 1.8rem;
  background: url("../assets/count_bg.png") no-repeat;
  background-size: 100% 100%;
  color: #fff;
  font-size: 0.7rem;
}
.index-page .count-wrapper .list-item .green-btn-icon {
  position: absolute;
  top: 1px;
  right: -1rem;
  display: inline-block;
  width: 1.6rem;
  height: 1.6rem;
  background: url("../assets/green_shade_icon.png") no-repeat;
  background-size: 100% 100%;
}
.index-page .count-wrapper .list-item span {
  display: block;
  height: 1.8rem;
  line-height: 1.8rem;
  width: 70%;
  margin: auto;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.index-page .count-wrapper .list-item .left {
  position: absolute;
  top: 1px;
  left: -0.8rem;
  display: inline-block;
  width: 1.7rem;
  height: 1.7rem;
}
.index-page .count-wrapper .list-item .left.gold-icon {
  background: url("../assets/gold_count_icon.png") no-repeat;
  background-size: 100% 100%;
}
.index-page .count-wrapper .list-item .left.money-icon {
  background: url("../assets/money_count_icon.png") center no-repeat;
  background-size: 100% 106%;
}
.index-page .count-wrapper .list-item .left.bx-icon {
  background: url("../assets/bx_count_icon.png") no-repeat;
  background-size: 100% 100%;
}
.index-page .bottom-menu {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
}
.index-page .bottom-menu ul {
  overflow: hidden;
}
.index-page .bottom-menu ul li {
  float: left;
  padding: 0.7rem 0;
  width: 20%;
  font-size: 0.8rem;
  text-align: center;
  color: #fff;
  -webkit-transition: all 0.2s linear;
  -webkit-transition-timing-function: cubic-bezier(0, 0.74, 0.52, 1.38);
}
.index-page .bottom-menu ul li.active {
  -webkit-transform: scale(1.2, 1.2) translate(0, -10px);
}
.index-page .bottom-menu ul li i {
  display: inline-block;
  width: 3rem;
  height: 3rem;
  margin: 0.1rem 0;
}
.index-page .bottom-menu ul li i.invite-icon {
  background: url("../assets/bottom_invite_icon.png") center 5px no-repeat;
  background-size: 100% 88%;
}
.index-page .bottom-menu ul li i.tx-icon {
  background: url("../assets/bottom_tx_icon.png") center 5px no-repeat;
  background-size: 100% 88%;
}
.index-page .bottom-menu ul li i.active-icon {
  background: url("../assets/bottom_active_icon.png") center 5px no-repeat;
  background-size: 100% 88%;
}
.index-page .bottom-menu ul li i.hobby-icon {
  background: url("../assets/bottom_hobby_icon.png") center 5px no-repeat;
  background-size: 100% 88%;
}
.index-page .bottom-menu ul li i.exchange-icon {
  background: url("../assets/bottom_exchange_icon.png") center 5px no-repeat;
  background-size: 100% 88%;
}
.index-page .member-header {
  position: relative;
  padding: 0.8rem 0.6rem;
  overflow: hidden;
}
.index-page .member-header .edit-icon {
  float: right;
  width: 2.6rem;
  height: 2.6rem;
  background: url("../assets/out_edit_icon.png") center no-repeat;
  background-size: 100% 100%;
}
.index-page .member-header .head-wrapper {
  float: left;
  padding: 0.3rem 0;
  width: 82%;
  overflow: hidden;
  color: #fff;
  background: url("../assets/count_lang_bg.png") no-repeat;
  background-size: 100% 100%;
}
.index-page .member-header .head-wrapper .jifen {
  position: relative;
  float: right;
  margin-right: 0.5rem;
  padding: 0 0.5rem 0 1rem;
  height: 1.95rem;
  line-height: 1.95rem;
  font-size: 0.9rem;
  background: url("../assets/count_bg.png") no-repeat;
  background-size: 100% 100%;
}
.index-page .member-header .head-wrapper .jifen .jf-icon {
  position: absolute;
  top: 0.2rem;
  left: -0.7rem;
  width: 1.5rem;
  height: 1.5rem;
  background: url("../assets/jb_icon.png") center no-repeat;
  background-size: 100% 100%;
}
.index-page .member-header .head-wrapper .head-img {
  float: left;
  margin-left: 1.1rem;
  overflow: hidden;
}
.index-page .member-header .head-wrapper .head-img img {
  float: left;
  display: block;
  width: 1.6rem;
  height: 1.6rem;
  border: 3px solid #fe8300;
  border-radius: 50%;
}
.index-page .member-header .head-wrapper .head-img span {
  float: left;
  padding-left: 0.4rem;
  height: 1.95rem;
  line-height: 1.95rem;
  font-size: 0.9rem;
}
.index-page .middle-menu .list-item {
  display: block;
  width: 12rem;
  height: 4rem;
  margin: 2.4rem auto;
}
.index-page .middle-menu .list-item.member {
  background: url("../assets/index_member_icon.png") center no-repeat;
  background-size: 100% 100%;
}
.index-page .middle-menu .list-item.xuezhan {
  background: url("../assets/index_xuezhan_icon.png") center no-repeat;
  background-size: 100% 100%;
}
.index-page .middle-menu .list-item.publiclist {
  background: url("../assets/index_public_icon.png") center no-repeat;
  background-size: 100% 100%;
}
.index-page .middle-menu .list-item:active {
  -webkit-transform: scale(1.1, 1.1);
}


